.menu-btn {
  color: #fff;
  cursor: pointer;
  display: block;
  position: fixed;
  top: 1.25rem;
  right: 30px;
  z-index: 1000;

  &:hover {
    opacity: .7;
  }
}

.nav-wrapper {
  background-color: #222;
  min-height: 75px;
  position: fixed;
  width: 100%;
  z-index: 100;
}

.main-nav {
  align-items: center;
  display: grid;
  font-size: 1.2rem;
  margin: 1rem 0;
  position: fixed;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  width: 100%;
  z-index: 100;
}

.logo {
  height: 50px;
  margin-left: 1.5rem;
  margin-top: -.25rem;
  width: auto;
}

.main-menu {
  background-color: #222;
  display: block;
  height: 100vh;
  text-align: center;
  top: 75px;
  transition: all .5s ease-in-out;
  transform: translateX(1000px);
  width: 100%;
  z-index: 100;

  &_show {
    transform: translateX(0px);
  }

  & a {
    color: #fff;

    &:hover {
      color: #c6ac8f;
    }
  }
}

.nav-link {
  margin: 1rem;
  padding: 1rem;
}

/* =========================== */
/* =======MEDIA QUERIES======= */
/* =========================== */
@media (min-width: 760px) {
  .menu-btn {
    display: none;
  }

  .main-nav {
    align-items: center;
    grid-template-columns: 1fr 3fr;
    height: 75px;
    justify-content: space-between;
    margin: 0;
  }

  .logo {
    margin-top: 0;
  }

  .main-menu {
    align-items: center;
    background: none;
    display: grid;
    font-size: 1rem;
    height: 75px;
    grid-template-columns: repeat(5, min-content);
    grid-template-rows: auto-fill;
    justify-content: end;
    margin: 0;
    text-align: center;
    transform: translateX(0);
    width: 100%;

    & a {
      padding: 0 1rem;

      &:last-child {
        padding-right: 0;
      }
    }
  }

  .nav-link {
    border: none;
    margin: 0;
  }
}